.btnForm{
  font-size: 20px;
  padding: 10px;
  border-radius: 30px;
  border: #087971 1px solid;
  box-shadow: #088178 1px 1px 10px ;

}

.formu{
  margin: 90px 0 0 0;
  width: 100%;
}

/* Dark Mode */
.darkMode{
  background-color: #000 !important;
  color: white !important;
}

.heroDark{
  background-image: url('./assets/img/hero4-dark.jpg');
  margin-top: 29px;
  width: 100%;
  height: 93vh;
  background-size:cover;
  background-position-y: 73px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  padding: 0 80px;
}

.heroDark h4 {
  padding-bottom: 15px;
  font-size: 40px;
}
.heroDark h2{
  font-size: 70px;

}
.heroDark h1 {
  color: #088178;
  font-size: 70px;

}
.heroDark button {
  background-color: transparent;
  background-image: url('./assets/img/button.png');
  background-repeat: no-repeat;
  background-size: cover;
  width: 260px;
  height: 60px;
  border: 0;
  cursor: pointer;
  font-size: 20px;
  font-weight: 700;
  color: #088178;
  padding: 14px 65px 14px 65px;
}
.headerDark {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: #e3e6f3;
  padding: 10px 80px;
  box-shadow: 0 5px 15px rgba(255, 255, 255, 0.839);
  z-index: 999;
  position:fixed;
  top: 0;
  left: 0;
  right: 0;
}

/* targetitas */ 
.sectionDark-p1 .f-box {
  
  width: 180px;
  text-align: center;
  padding: 25px 15px;
  box-shadow: 20px 20px 34px rgba(255, 255, 255, 0.557);
  border: 1px solid #cce7d0;
  border-radius: 4px;
  margin: 60px 0 20px 0 ;
  transition: box-shadow 0.3s;
}
.sectionDark-p1 .f-box:hover {
  box-shadow: 10px 10px 54px rgba(255, 255, 255, 0.1);
}
.sectionDark-p1 .f-box img {
  width: 100%;
  margin-bottom: 10px;
}
.sectionDark-p1 .f-box h6 {
  display: inline-block;
  padding: 9px 8px 6px 8px;
  line-height: 1;
  font-size: 14px;
  border-radius: 4px;
  color: #088178;
  background-color: #fddde4;
}
.sectionDark-p1 .f-box:nth-child(2) h6 {
  background-color: #cdebbc;
}
.sectionDark-p1 .f-box:nth-child(3) h6 {
  background-color: #d1e8f2;
}
.sectionDark-p1 .f-box:nth-child(4) h6 {
  background-color: #cdd4f8;
}
.sectionDark-p1 .f-box:nth-child(5) h6 {
  background-color: #f6dbf6;
}
.sectionDark-p1 .f-box:nth-child(6) h6 {
  background-color: #fff2e5;
}
